body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer{
  height: 10vh;
  background: -webkit-linear-gradient(#555555, #aaa);
  background: linear-gradient(#555555, #aaa);
}

.bottom-icon{
  height: 15vh;
}
.logo-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-weight: 600;
}

.logo-logo {
  -webkit-animation: logo-spin infinite 20s linear;
          animation: logo-spin infinite 20s linear;
  height: 15vh;
  margin: 20px;
}

.logo-info{
  margin: 15px;
}

.logo-name{
  margin: 0;
  color: #9d9d9d;
}

.logo-link {
  color: #921816;
  text-decoration: none;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Navbar{
  display: flex;
  height: 10vh;
  justify-content: space-between;
  align-items: flex-end;
  background: -webkit-linear-gradient(#282c34, #555555);
  background: linear-gradient(#282c34, #555555);
  margin: 0;
  border: none;
}

.Nav-menu{
  display: flex;
  list-style: none;
  width: 40vw;
}

.Nav-menu-item{
  padding: 10px;
  font-size: 22px;
  font-weight: 600;
  margin: 0  10px 0 0;
  cursor: pointer;
  text-shadow: 0 0 20px #921816;
}

.Nav-menu-item.chosen{
  color: #fff;
}



.form-container{
  width: 40vw;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search{
  display: flex;
  height: 6vh;
  width: 30vw;
  justify-content: flex-end;
  align-items: center;
  
}

.search-icon{
  width: 6vh;
  height: 6vw;
  cursor: pointer;
}

.search-field{
  width: 0px;
  height: 4vh;
  border: none;
  font-size: 16px;
  border-radius: 10px;
  color: white;
  font-weight: 600;
  background-color: #aaa;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
}

.search-field.open{
  padding-left: 10px;
  width: 200px;
}

.search-field.close{
  pointer-events:none;
  padding-left: 10px;
  width: 200px;
  color: black;
  background-color: #921816;
}
.modal-wrapper{
  width:100vw;
  height:100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position:fixed;
  top:0; left:0;
  background:rgba(146,24,22,0.75);
  visibility:hidden;
  opacity:0;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

.modal-wrapper.open{
  opacity:1;
  visibility:visible;
}

.modal{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  opacity: 0;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.modal-wrapper.open .modal{
  opacity:1;
}

.modal-item-content{

  border-radius: 15px;
  background: #fff; 
}

.btn-close{
  width:32px;
  height:32px;
  margin: 20px;
  display:block;
  float:right;
}

.btn-close::before, .btn-close::after{
  content:'';
  width:32px;
  height:6px;
  display:block;
  background: #000;
}

.btn-close::before{
  margin-top:12px;
  -webkit-transform:rotate(45deg);
  transform:rotate(45deg);
}

.btn-close::after{
  margin-top:-6px;
  -webkit-transform:rotate(-45deg);
  transform:rotate(-45deg);
}

.info-list{
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  list-style-type: none;
}

.item-field{
  margin: 5px 15px 5px 15px;
}

.item-data{
  font-weight: 600;
}
.content{ 
  height: 50vh;
  margin: 0;
  padding: 0;
  background-color: #555555;
  border: none;
}

.main-yoda-logo{
  height: 25vh;
  width: 25vw;
}
.content-main{
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 18px;
  text-align: left;
  text-shadow: 0 0 20px #8b9e6c;
}

.content-loading{
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-icon{
  -webkit-animation: logo-spin infinite 1s linear;
          animation: logo-spin infinite 1s linear;
  height: 15vh;
  margin: 20px;
}

.blur{
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}

.content-list{
  height: 50vh;
  width: 100vw;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.left-arrow{
  cursor: pointer;
  width: 200px;
  margin: 0 20px 0 20px;
}

.left-arrow:hover{
  -webkit-animation: left-arrow-move infinite 1s linear;
          animation: left-arrow-move infinite 1s linear;
}

.left-arrow img{
  width: 50px;
  height: 200px;
  -webkit-transform:rotate(-90deg);
  transform:rotate(-90deg);
}

.right-arrow{
  cursor: pointer;
  width: 200px;
  margin: 0 30px 0 30px;
}

.right-arrow:hover{
  -webkit-animation: right-arrow-move infinite 1s linear;
          animation: right-arrow-move infinite 1s linear;
}

.right-arrow img{
  width: 30px;
  height: 200px;
  -webkit-transform:rotate(90deg);
  transform:rotate(90deg);
}

#greensaber{
  -webkit-filter: drop-shadow(0px -70px 8px #8cbe3c);
          filter: drop-shadow(0px -70px 8px #8cbe3c);
}

#bluesaber{
  -webkit-filter: drop-shadow(0px -70px 8px #1eaae1);
          filter: drop-shadow(0px -70px 8px #1eaae1);
}

.items-list{
  width: 50vw;
  margin-left: 0;
  padding-left: 0;
}

.trigger{
  list-style-type: none;
  cursor: pointer;
  font-size: 22px;
  margin: 5px 0 5px 0;
  text-shadow: 0 0 20px #921816;
}

.trigger:hover{
  color: #921816;
  -webkit-filter: invert(100%);
          filter: invert(100%);
  z-index: -1;
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes left-arrow-move {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px)
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes left-arrow-move {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px)
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@-webkit-keyframes right-arrow-move {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(15px);
            transform: translateX(15px)
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes right-arrow-move {
  from {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(15px);
            transform: translateX(15px)
  }
  to {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
.App {
  text-align: center;
  height: 100vh;
  width: 100vm;
  background-color: #aaa;
}

